.mob_price {
	display: none;
	width: 100%;
	height: 750px;;
}

.price__wraper {

	display: flex;

	justify-content: center;
	flex-wrap: wrap;
	width: 100%;
}
.price__title {
	display: flex;
    justify-content: center;
    flex-wrap: wrap;
    text-transform: uppercase;
    text-align: center;
    width: 100%;
}
.price__title h1 {
	color: #4a2c79;
    margin-bottom: 20px;
    text-transform: uppercase;
}

.price__wrapp{
	display: flex;
	flex-flow: column;
	flex-wrap: wrap;
	justify-content: center;
	max-width: 1080px;
	margin-bottom: 25px;

}


.price__wrapp h1 {
	margin-top: 35px;
	margin-bottom: 5px;
	font-size: 25px;
	color: white;
	text-transform: uppercase;
	width: 100%;
	text-align: center;
	background-color: #49287a;
}
.first_h1{
	margin-top:1px !important;

}
.price__wrapp p{
	margin-bottom: 2px;
}
.price__wrapp tr {
	text-transform: none;
}
td {
	text-align: center;
	border: 3px solid #4a2c79;

}
.price__wrapp tr:hover{
	color:white;
}





.cardboard_wrapp {
  display: flex;
	margin-top: 5px;
  justify-content: center;
  width : 100%;
  height: 30px;

}

.cardboard {
  display: flex;
	align-items: center;
	align-content: center;


  flex-grow:row;
  justify-content:flex-start;
	width: 1080px;
  max-width : 1080px;
  height: 30px;


}
.circle {
	border: 1px solid #49257c;
  display:flex;
  width: 20px;
  height: 20px;
  margin-left:5px;
  margin right:5px;
  border-radius: 50%;
}
















