.mob_logo_slider {
	display: none;

}


.myLogos_slider {
	display: flex;
	margin-top: 25px;
	margin-bottom: 25px;
	justify-content: center;
	width: 100%;

}

.Logos_slider {

	height: 50px;
	width: 80%;

	max-width: 1080px;


}
.BrainhubCarousel__arrows {
	background-color: #49287a;
}

.BrainhubCarousel__arrows:hover {
	background-color: #9c72c4;
}
button:focus {
	outline: none;
}

.Logos_slider img {
	margin-right: 10px;
	margin-left: 10px;
}










/*для мобилки */









@media (max-width: 576px) {

.mob_logo_slider {
	display: block;

}

.myLogos_slider {
	display: none;
}

}
