.Article_wrapp{
	margin-top: 30px;
	display: flex;
	width: 100%;
	justify-content: center;
}

.article__box{
  display: flex;

  justify-content: center;
  height:100%;
  width: 1080px;

}
.article__content {
  display: flex;
  flex-flow: column;

  /*border: 2px solid green;*/

  height: 100%;
  width : 800px;
	margin-left: 30px;
  max-width: 800px;

  margin-bottom:20px;

}
.article__title{
  display: flex;
  width: 100%;
  justify-content: center;
  height: 40px;

  /*border: 1px solid red;*/

}

.article__description{
  display: flex;
	margin-top: 30px;
  width: 100%;
	flex-flow: column;
  justify-content: center;

  /*border: 5px solid grey;*/

  height:100%;
}
.article_predescription {
	display: flex;
  width: 100%;

	height:100%;

}
.article_pretext {
  display: flex;
	flex-flow: column;
	text-align: justify;
  width: 480px;
	margin-right: 15px;

  /*border: 3px solid green;*/

	height: 100%;

}

.article_pretext h4 {
	color: #4a2c79;
	font-size: 25px;
	text-transform: uppercase;
	text-align: center;
}


.article_text {
	text-align: justify;
  display: flex;
	flex-flow: column;
  width: 100%;
	margin-top: 15px;

  /*border: 3px solid yellow;*/

	height: 100%;


}

.article_text h4 {
	color: #4a2c79;
	font-size: 25px;
	text-transform: uppercase;
	text-align: center;
}
.article_text ol>li{
	list-style: decimal;

}
.article_text ul>li{
	list-style: disc;

}
.article__description__price {
	display: flex;
	width: 300px;
	height: 100%;
	flex-flow: column;
}

.article__img {
  display: flex;
  width: 300px;
  height: 300px;

  /*border: 5px solid purple;*/

}
.article__price {
	display: flex;
	justify-content: center;
	margin-top:10px;
	width: 100%;
	height: 50px;
	background:#4a297a;

}

.article__price p{
	color: white;
	text-align: center;
	font-size: 23px;
  margin: 10px 0px 10px 0px;
	text-transform: uppercase;
}

.consultation {
	display: flex;
	justify-content: center;
	width: 100%;
	height: 40px;
	margin-bottom: 20px ;
	background-color: #4a297a
}

.consultation p{
	color: white;
	text-align: center;
	font-size: 17px;
  margin: 10px 0px 10px 0px;
	text-transform: uppercase;
}



@media screen and (min-width: 290px) and (max-width: 800px) {

.leftnav {
	display: none !important;
}
.article__title{
	height: 100%;
	text-align: center;
}

	.article__box {
		width: 100%;
	}
	.article__content {
		margin: 0px;
		width: 100%;
		justify-content: center;
		flex-direction: column;
		margin-left: 15px;
		margin-right: 15px;

	}
	.article_predescription {
		display: flex;
		justify-content: center;
		flex-flow: column-reverse;

	}
	.article_pretext {
		width: 100%;
	}
	.article__description {
		display: flex;
		justify-content: center;
		flex-flow: column;
		margin-top: 15px;

	}
	.article__description__price{
		width: 100%;
		justify-content: center;
		align-items: center;
		margin-bottom: 20px;
	}
	.article__img {
		justify-content: center;
		align-items: center;
	}
	.article_text {
		margin-top: 0px;
	}
	.consultation {
		width: 100%;
		height: 100%;
		padding-left: 20px;
		padding-right: 20px;
	}
	.article_text>table {
		display: none;
	}



}
