.contact {

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
	margin-top: 20px;


}

.mycontact {
  display: flex ;
  width:45%;
   flex-wrap: wrap;
  margin-right:30px;

}
.contact_title {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;

}
.contact_title h1{
color: #4a2c79;
text-transform: uppercase;

}
.contact_information {
   display: flex;
   text-align: justify;
  justify-content: center;
  width: 100%;
	margin-left: 60px;
	flex-flow: column;

}
.contact_information__wrapp {
	display: flex;
	height: 25px;
	justify-content: start;
	margin-bottom: 5px;
	width: 100%;
}

.contact_information__wrapp i {
	font-size: 20px;
	font-weight: 400;
}
.contact_information__icon {
	display: flex;
	justify-content:flex-start;
	align-items: baseline;
	width: 20px;
	height: 35px;
	margin-right: 10px;


}
.contact_information__icon i {
	color:rgb(74, 41, 122);


}
.contact_information__text {
	display: flex;
	justify-content: start;
	align-items: baseline;
	width: 80%;
	height: 40px;

}
.contact_information__text p{
	margin: 0px 0px 0px 0px;

}
.contact_requisites {
  display: flex;
  justify-content: center;
  width: 45%;
   flex-wrap: wrap;
  margin-left: 30px;

}
.contact_requisites_info {
  display: flex;
  justify-content: center;
  text-align: justify;
  width: 100%;

}
.contact_map{
	margin-left: 20px;
	margin-right: 20px;
	margin-bottom: 30px;
}


@media screen and (min-width: 290px) and (max-width: 800px) {

	.contact_requisites {
		width: 100% !important;
		margin-left: 0px !important;
	}
	.mycontact {
		width: 100% !important;
		margin-right: 0px !important;
	}
	.contact_information {
		margin-left: 0px;
	}
	.contact_information__text {
		width: 100%;
	}
	.contact_information__text #insta{
	display: none;
	}

}
