.preloader {
	height: 60%;
}
.mobile_news__card {
	display: none;
}



.news_box__wrapp{
	display: flex;
	justify-content: center;




}



.news {


  display: flex;
	flex-flow: column;
  width: 100%;
	height: 100%;
  flex-wrap: wrap;


}
.news_title__box{
	display: flex;
	width: 100%;
	justify-content: center;
	height: 50px;

}
.news__title {
	display: flex;
	width: 80%;
	justify-content: center;
	height: 50px;

}
.news__title h1 {

	color: #4a2c79;
	    text-transform: uppercase;
}



.news_card {
  display:flex;

  width: 100%;
  height: 400px;
  margin:20px;

}
.news_card_info {
  flex-wrap:wrap;
  display:flex;
  width: 100%;
  height:250px;
  justify-content: center;


}
.news_date_title {
   height:30px;
  display: flex;
  width: 100%;


  justify-content: flex-start;
}
.news_date {
  background-color: #4a2c79;
  display:flex;
  width:90px;
   height:30px;

  justify-content:flex-start;
}
.news_date p {
  text-align: center;
  color: white;
	margin: 5px 10px 5px 10px;
}

.news_title {
  display:flex;
  width:100%;
  align-items: center;
  justify-content:center;
}

.news_title h3 {
	color: #4a2c79;
	margin: 10px 10px 5px 0px;
	    text-transform: uppercase;
}

.news_info {
  display:flex;
  width:100%;
	height: 100%;
	margin-top: 10px;
	flex-flow: column;
  text-align: justify;

}

.news_card_foto {
  display:flex;
  flex-wrap:wrap;
	width: 60%;
  justify-content:flex-end;

}

.news_foto {
  display:flex;
  justify-content:center;
  height: 185px;
  width :290px;

}
.news_button {
  display:flex;
  background-color: #4a2c79;
  width:290px;
  height: 40px;
  justify-content: center;

}

.news_button a{
	color: white !important;
	text-transform: uppercase;
	font-size: 20px;

}
.btn{
	color: white !important;

}
.news_button a:hover{
	color: #9c6fc6 !important;

}


@media screen and (min-width: 290px) and (max-width: 800px) {

	.left_menu_navigation {
		display: none !important;
	}
	.desktop_news{
		display: none !important;
	}

	.mobile_news__card {
		display: block;
	}



}
