.wrapp__delivery {
	display: flex;
  justify-content: center;

	width: 100%;




}
.delivery {
	display: flex;


	max-width: 1080px;


	flex-flow: column;

}





.delivery_zone {
  flex-wrap:wrap;
	flex-flow: column;
  display:flex;


  justify-content:center;

}
.delivery_map {

  display: flex;
	justify-content: center;
  height: 300px;
	width: 100%;


	margin-bottom: 30px;
}

.mydelivery {
  display: flex ;
  width:45%;
  flex-wrap: wrap;
  margin-right:30px;

}

.delivery_title h1 {

	text-transform: uppercase;
	color: #4a2c79;
	font-size: 35px;


}



.delivery_title {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
}
.delivery_info_price {
   display: flex;
	 flex-flow: column;
   text-align: justify;
  justify-content: center;
  width: 100%;

}

.delivery_condition {
  display: flex;
  justify-content: center;
  width: 45%;
   flex-wrap: wrap;
  margin-left: 30px;

}
.delivery_info {
  display: flex;
  justify-content: center;
  text-align: justify;
  width: 100%;

}





@media screen and (min-width: 290px) and (max-width: 800px) {

	.delivery_condition{
		width: 100% !important;
		margin-left: 0px !important;
	}
	.mydelivery{
		width: 100% !important;
		margin-right: 0px !important;
	}
	.delivery_title h1 {
    
    font-size: 33px;
	}
	.mobb{
		flex-flow: column !important;
	}
	.delivery_info_price p {
		font-size: 15px;
	}
	.delivery_info ol>li{
		list-style: decimal;

	}
	 .delivery_info li {
		font-size: 15px;


	}




}
